import React, {useState, useEffect} from 'react';
import Calendar from '../../components/Calendar';
import './CalendarView.css'
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import LeftIcon from '@mui/icons-material/ArrowCircleLeft'
import RightIcon from '@mui/icons-material/ArrowCircleRight'

import { Day } from '../../models/CalendarData';
import { fetchData } from '../../services/CalendarDataService';

const monthNames = ['', '', '', '', '', '', '', '', '', '', '', '']

const CalendarView = () => {
    const [daysData, setDaysData] = useState<Array<Day>>([]);
    const [year, setYear] = useState(2025);
    const [month, setMonth] = useState(1);
    // const [year, setYear] = useState(new Date().getFullYear());
    // const [month, setMonth] = useState(new Date().getMonth() + 1);
    useEffect(() => {
        (async () => {
            const data = await fetchData(year, month)
            setDaysData(data.days);
        })();
    }, [year, month]);

    const changeMonth = (delta: number) => {
        if (month + delta < 1) {
            setYear(year - 1);
            setMonth(12);
        } else if (month + delta > 12) {
            setMonth(1);
            setYear(year + 1);
        } else {
            setMonth(month + delta);
        }
    };

    return (
        <div className='calendar-view'>
            <Typography variant="h5" color="inherit" component="div">
                <IconButton onClick={() => changeMonth(-1)}>
                    <LeftIcon />
                </IconButton>
                {monthNames[month - 1]} {year}
                <IconButton onClick={() => changeMonth(1)}>
                    <RightIcon />
                </IconButton>
            </Typography>
            <Calendar year={year} month={month} daysData={daysData}/>
        </div>
    );
};

export default CalendarView;