import React from 'react';
import { Day, Phase } from '../../models/CalendarData';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import { formatActivity } from './Utils'

interface Props {
    day: number;
    isCurrent: boolean;
    isWeekend: boolean;
    data: Day;
}

/*
https://www.alt-codes.net/planet-symbols.php
https://www.alt-codes.net/zodiac-signs.php
*/
const CalendarDay = (props: Props) => {
    const classes = ['calendar-table-date'];
    props.isCurrent && classes.push('today');
    props.isWeekend && classes.push('weekend');

    const mapMoonPhase = (phase: Phase) => {
        switch (phase) {
            case Phase.first_q: return ''
            case Phase.full_moon: return ''
            case Phase.last_q: return ''
            case Phase.new_moon: return ''
        }
    }

    return props.day === 0 ? <div></div> : (
        <div className='calendar-table-day'>
            <Typography variant="h5" color="inherit" component="div" className={classes.join(' ')}>
                {props.data.moon_phase && mapMoonPhase(props.data.moon_phase.phase)}
                &nbsp;&nbsp;
                {props.day}
            </Typography>

            <div className='calendar-table-content'>
                {props.data?.moon?.map((r, index) => (
                    <Typography key={index} variant="body2" color="inherit" component="div" sx={{ fontFamily: 'astro', fontWeight: r.highlight ? 'bold' : 'normal'}}>
                        {formatActivity(r.activity, r.time)}
                    </Typography>
                ))}
            </div>
            {props.data?.planets?.length > 0 && <Divider />}
            <div className='calendar-table-content'>
                {props.data?.planets?.map((r, index) => (
                    <Typography key={index} variant="body2" color="inherit" component="div" sx={{ fontFamily: 'astro', fontWeight: r.highlight ? 'bold' : 'normal'}}>
                        {formatActivity(r.activity, r.time)}
                    </Typography>
                ))}
            </div>
        </div>
    );
};

export default CalendarDay;