import React, {useState, useEffect} from 'react';
import CalendarDay from './CalendarDay';
import LunarDay from './LunarDay';
import { getDaysArray } from './Utils';
import { Day } from '../../models/CalendarData';

import './Calendar.css'

interface Props {
    year: number;
    month: number;
    daysData: Day[];
}

const Calendar = (props: Props) => {
    const [days, setDays] = useState<Array<Array<number>>>([]);
    const [currentDate, setCurrentDate] = useState<number>(0);
    useEffect(() => {
        setDays(getDaysArray(props.year, props.month))
        setCurrentDate(new Date().getDate());
    }, [props.year, props.month]);

    const now = new Date();
    const isCurrentMonth = (now.getFullYear() === props.year) && (now.getMonth() === props.month - 1);

    return (
        <div>
            <table className='calendar-table'>
                <thead>
                    <tr>
                        {['', '', '', '', '', '', ''].map(wd => <th key={wd}>{wd}</th>)}
                    </tr>
                </thead>
                <tbody>
                    {mapToWeeks(days, currentDate, isCurrentMonth, props.daysData)}
                </tbody>
            </table>
        </div>
    );
};

const mapToWeeks = (days: Array<Array<number>>, currentDate: number, isCurrentMonth: boolean, daysData: Array<Day>): Array<JSX.Element> => {
    return days.map((week, index) => mapToWeek(week, index, currentDate, isCurrentMonth, daysData)).flat();
};

const mapToWeek = (week: Array<number>, index: number, currentDate: number, isCurrentMonth: boolean, daysData: Array<Day>): Array<JSX.Element> => {
    return [
        (
        <tr key={`week-${index}`}>
            {mapToDays(week, currentDate, isCurrentMonth, daysData)}
        </tr>
        ), 
        (
        <tr key={`week-lunar-${index}`}>
            {mapToLunarDays(week, daysData)}
        </tr>
        )
    ];
};

const mapToDays = (days: Array<number>, currentDate: number, isCurrentMonth: boolean, daysData: Array<Day>): Array<JSX.Element> => {
    return days.map((day, index) => mapToDay(day, index, currentDate, isCurrentMonth, daysData));
};

const mapToDay = (day: number, index: number, currentDate: number, isCurrentMonth: boolean, daysData: Array<Day>): JSX.Element => {
    const dayData = daysData.find(d => d.day === day) || {} as Day;
    return (
        <td key={`day-${index}`}>
            <CalendarDay day={day} isCurrent={isCurrentMonth && day === currentDate} isWeekend={index >= 5} data={dayData} />
        </td>
    );
};

const mapToLunarDays = (days: Array<number>, daysData: Array<Day>): Array<JSX.Element> => {
    return days.map((day, index) => mapToLunarDay(day, index, daysData));
};

const mapToLunarDay = (day: number, index: number, daysData: Array<Day>): JSX.Element => {
    const dayData = daysData.find(d => d.day === day) || {} as Day;
    return (
        <td key={`day-${index}`}>
            <LunarDay day={day} data={dayData} />
        </td>
    );
}


export default Calendar;