import {Month} from '../models/CalendarData'

import data from './calendar.json';

const fetchData = async (year: number, month: number): Promise<Month> => {
    const years = data.find(y => y.year === year) || {
        months: []
    };
    const months = years.months.find(m => m.month === month) || {
        days: []
    };
    return {
        month: month,
        days: months.days
    } as Month;
};

export {
    fetchData
}